
.todo-form {
    text-align: center;
}

.add-todo-button {
    cursor: pointer;
}

.todo-section {
    max-width: 500px;
    margin: 50px auto;
}

.todo-form__heading {
    text-align: center;
}

.todo-icon {
    font-size: 22px;
}

.todo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    width: 100%;
    list-style: none;
    text-align: left;
    border-bottom: 1px solid dimgrey;
    height: 60px;
    opacity: 70%;
}

.todo-item:hover {
    opacity: 100%;
}

.item-wrap:before {
    content: '';
    height: 100%;
    vertical-align: middle;
    display: inline-block;
}

.item-wrap {
    width: 100%;
    cursor: pointer;
    height: 100%;
}

.todo-item-value, .checkbox {
    vertical-align: middle;
    display: inline-block;
}

.checkbox {
    margin-right: 20px;
}

.delete-item-button,
.add-item-button
{
    border: none;
    background: transparent;
    cursor: pointer;
}

input[type="checkbox"] {
    display: none;
}

.add-item-button, .add-todo-button {
    vertical-align: middle;
}

.user-value {
    padding: 5px;
    outline: none;
    width: 300px;
}

.marked {
    text-decoration: line-through;
}